body {
  background-color: #F3F3F3;
}

.Card {
  height: 323px;
  width: 222px;
  background-color: white;
  border-radius: 20px;
  border: 3px solid black;
  display: inline-block;
}

.MyCards{
  position: absolute;
  bottom:10px;
  width: 474px;
  height: 353px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  -webkit-transform:scale(.75);
      -ms-transform:scale(.75);
          transform:scale(.75);
  -webkit-transform-origin: bottom;
      -ms-transform-origin: bottom;
          transform-origin: bottom;

  background-color: white;
  border-radius: 10px;
  padding:10px;
  -webkit-box-shadow: 0px 2px 5px 0px grey;
          box-shadow: 0px 2px 5px 0px grey;
}

.MyCards div:nth-child(2){
  margin-left: 10px;
}

.GlobalCards{
  max-width: 1170px;
  height: 353px;
  top:60px;

  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;

  text-align: center;

  background-color: white;
  border-radius: 10px;
  padding:10px;
  -webkit-box-shadow: 0px 2px 5px 0px grey;
          box-shadow: 0px 2px 5px 0px grey;
}

.GlobalCards div{
  display: inline-block;
  margin:0px 4px;
}

.Options{
  width:100px;
  position: absolute;
  bottom: 10px;
  right:calc(50% - 300px);
  opacity: 0;
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  -webkit-box-shadow: 0px 2px 5px 0px grey;
          box-shadow: 0px 2px 5px 0px grey;
}

.Options button{
  width:80px;
  margin-bottom:5px;
}

.Player{
  width:250px;
  /* height: 150px; */
  position: absolute;
  word-wrap: break-word;

  -webkit-transition: background-color 1s, color 1s;

  -o-transition: background-color 1s, color 1s;

  transition: background-color 1s, color 1s;

  background-color: white;
  border-radius: 10px;
  padding: 10px;
  -webkit-box-shadow: 0px 2px 5px 0px grey;
          box-shadow: 0px 2px 5px 0px grey;
}

.player-card{
  display: inline-block;
  width: 110px;
  height: 161px;
  background-size: 110px 161px;
  float: left;
}

.Player:nth-child(1){
  bottom:10px;
  right:calc(50% + 200px);
  height: 265px;
}

.Player:nth-child(2){
  top:20%;
  left:10px;
}

.Player:nth-child(3){
  bottom:20%;
  left:10px;
}

.Player:nth-child(4){
  top:20%;
  right:10px;
}

.Player:nth-child(5){
  bottom:20%;
  right:10px;
}



